
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import { ScenceReleaseListDetails } from "@/servers/api";
import { timeTodate } from "@/assets/js/tools";
import { setTimeout } from "timers";

let count = 1;
let totalCount = 0;
export default {
name: "",
props: [ "scrollTop", "pullDownRefreshObj"],
data() {
return {
scrollbarObj: {
fade: true
},




pullUpLoadObj: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多数据了"
}
},
startY: 0, // 纵轴方向初始化位置
scrollToX: 0,
scrollToY: 0,
scrollToTime: 700,
items: [],
commentCount: 99,
contentList: [],
page: 1,
totalNum: 0
};
},
filters: {
formTime(val) {
return timeTodate(val);
}
},
components: {
InlineLoading
},

computed: {
newArr() {
return this.$parent.commentListData;
}
},

beforeMount() {},

mounted() {
this.onPullingDown();
},
watch: {
newArr(val) {
if (val) {
this.onPullingDown();
}
}
},
methods: {

getCommintList() {
return new Promise(resolve => {
let arr = [];
ScenceReleaseListDetails({
id: this.$route.query.id,
type: this.$route.query.type,
page: this.page
})
.then(res => {
if (res.result === 1) {
totalCount = res.data.totalofnum;
this.totalNum = res.data.totalofnum;
setTimeout(() => {
if (res.data.comment.length) {
arr = res.data.comment;
resolve(res.data.comment);
} else {
this.$nextTick(() => {
this.$refs.scroll.forceUpdate(false);
});
}
}, 1000);
}
})
.catch(err => {
console.log(err);
});
});
},

scrollTo() {
this.$refs.scroll.scrollTo(
this.scrollToX,
this.scrollToY,
this.scrollToTime
);
},
onPullingDown() {
totalCount = 0;
this.page = 1;
this.getCommintList().then(res => {
this.contentList = res;
this.$nextTick(() => {
this.$refs.scroll.forceUpdate(true);
});
});
},
onPullingUp() {
this.page += 1;
this.getCommintList().then(res => {
this.contentList = this.contentList.concat(res);
if (this.contentList.length < totalCount) {
this.$refs.scroll.forceUpdate(true);
} else {
this.$refs.scroll.forceUpdate(false);
}
});
}
}
};



