import Header from "@/components/common/Header";
import Comments from "@/components/common/Comments";
import GiveLike from "@/components/common/GiveLike";
import DividedArea from "@/components/common/DividedArea";
import CommentList from "@/components/layout/CommentListPlay";
import { ScenceReleaseListDetails } from "@/servers/api";

export default {
  name: "",
  props: [""],
  data() {
    return {
      TitleObjData: {
        titleContent: "发布详情",
        showLeftBack: true,
        showRightMore: false
      },
      contentData: {},
      commentListData: [],
      currentId: "",
      clickState: 0,
      pullDownRefreshObj: {
        threshold: 40,
        stop: 40
      }
    };
  },

  components: {
    Header,
    Comments,
    GiveLike,
    DividedArea,
    CommentList
  },

  computed: {
    contentStyle() {
      return { height: document.documentElement.clientHeight - 45 + "px" };
    }
  },

  beforeMount() {},

  mounted() {
    this.getData();
  },

  methods: {
    // 更新点赞状态
    refreshData(type) {
      this.clickState = type;
      this.videoId = this.$route.query.id;
      ScenceReleaseListDetails({
        id: this.$route.query.id,
        type: this.$route.query.type,
        page: 1
      })
        .then(res => {
          if (res.result === 1) {
            this.contentData = res.data.content;
            this.commentListData = res.data.comment;
            this.clickState = res.data.content.type;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //更新评论数据列表
    updateCommintList() {
      let arr = [];
      ScenceReleaseListDetails({
        id: this.$route.query.id,
        type: this.$route.query.type,
        page: 1
      })
        .then(res => {
          if (res.result === 1) {
            if (res.data.comment.length) {
              arr = JSON.parse(JSON.stringify(res.data.comment));
            }
          }
          this.commentListData = arr;
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 初始化数据
    getData() {
      this.currentId = this.$route.query.id;
      ScenceReleaseListDetails({
        id: this.$route.query.id,
        page: 1
      })
        .then(res => {
          if (res.result === 1) {
            this.contentData = res.data.content;
            this.commentListData = res.data.comment;
            this.clickState = res.data.content.type;
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },

  watch: {}
};



